@import "../common/variables";
@import "../common/mixins";

.about-company {
  display: block;
  padding-bottom: 66px;
  overflow: hidden;

  @media (max-width: 768px) {
    margin-bottom: 58px;
  }
}

.about-company__title {
  margin-bottom: 43px;

  @media (max-width: 768px) {
    margin-bottom: 24px;
  }
}

.about-company__flex {
  display: flex;
  margin-bottom: 113px;

  @media (max-width: 768px) {
    flex-direction: column-reverse;
    max-width: 567px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 70px;
  }
}

.about-company__content {
  width: 100%;
  max-width: 473px;
  flex-shrink: 0;
  margin-right: 40px;
  padding-top: 23px;

  p {
    color: $text-color;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.05;
    margin-bottom: 1em;
  }

  @media (max-width: 900px) {
    max-width: 50%;
  }

  @media (max-width: 768px) {
    max-width: unset;
    display: flex;
    flex-direction: column;
  }
}
.about-company__banner {
  flex-grow: 1;
}

.about-company__banner-iframe-wrap {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-top: 56.25%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background-blend-mode: multiply;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 5px 10px 35px rgba(0, 0, 0, 0.2);
  background-size: cover;
  transition: 0.25s;
  border-radius: 20px;

  iframe {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: 20px;
  }
}

.about-company__card-btn {
  display: inline-block;
  margin-bottom: 13px;

  @media (max-width: 768px) {
    align-self: center;
    margin-top: 20px;
  }

  @media (max-width: 475px) {
    width: 100%;
    max-width: unset;
    margin-top: unset;
  }
}

.about-company__benefits {
  display: flex;
  align-items: center;
  position: relative;

  @media (max-width: 768px) {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.about-company__benefit {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #fff;
  box-shadow: 0 4px 14px rgba(58, 62, 62, 0.3);
  border-radius: 10px;
  padding: 23px 10px 26px;
  margin-right: 15px;

  @media (max-width: 768px) {
    margin-bottom: 22px;
    padding: 20px 10px 16px;
  }

  @media (max-width: 475px) {
    max-width: 180px;

    margin-right: unset;

    &:last-of-type {
      margin-left: 10px;
    }
  }

  @media (max-width: 413px) {
    max-width: unset !important;
    width: 100% !important;

    &:last-of-type {
      margin-left: unset;
    }
  }

  &--big {
    padding: 43px 40px 37px;

    @media (max-width: 768px) {
      margin-left: 100%;
      margin-right: 100%;
      flex-shrink: 0;
      width: 262px;
      padding: 20px 24px;
    }
  }
}

.about-company__benefit-title {
  font-size: 35px;
  color: $orange;
  font-weight: 700;
  margin-bottom: 23px;

  span#counter1 {
    font-size: 40px;
  }

}

.about-company__benefit-text {
  max-width: 183px;
  text-align: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 15px;

  @at-root .about-company__benefit--big #{&} {
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
  }
}

.about-company__benefits-map {
  position: absolute;
  bottom: -86px;
  z-index: -1;
  left: 600px;
  width: 1405px;
  height: 530px;
  max-width: unset;
  background-image: url(../media/map.png);
  background-size: contain;

  @media (max-width: 900px) {
    left: 40%;
  }

  @media (max-width: 768px) {
    left: 50%;
    transform: translateX(-50%);
    width: 1030px;
    height: 345px;
    background-size: cover;
    background-position-y: bottom;
  }

  @media (max-width: 767px) {
    background-image: url(../media/map-mob.png);
    width: 650px;
    height: 450px;
  }
}
