@import "variables";
@import "mixins";

html {
  box-sizing: border-box;
}

.no-scroll {
  overflow: hidden;
}

//----------------------------------------------------------------------

*,
*::before,
*::after {
  box-sizing: inherit;
  //transition: 0.3s;
}

body {
  font-family: $main-font;
  font-size: 14px;
  background-color: $bg-color;
  color: $text-color;
  padding-bottom: 70px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
