@import "../common/variables";
@import "../common/mixins";

.consultation {
  @include container;

  display: block;
  margin-bottom: 30px;
}

.consultation__title {
  margin-bottom: 15px;
}

.consultation__flex {
  display: flex;
  align-items: flex-start;
}

.consultation__form-wrap {
  width: 100%;
  max-width: 500px;

  input[type="text"],
  input[type="email"],
  input[type="number"],
  input[type="password"],
  input[type="tel"],
  select,
  textarea {
    background-color: white;
  }

  .form__btn {
    max-width: 192px;
    width: 100%;
  }
}

.consultation__form-text {
  text-align: center;
  font-size: 18px;
  line-height: 1.2;
  margin-bottom: 23px;
}


@media (max-width: 900px) {
  .consultation__flex {
    justify-content: center;
  }

  .consultation__img-wrap {
    display: none;
  }

  .form__file-wrap {
    margin-left: auto;
    margin-right: auto;
  }
}
