@import "../common/variables";
@import "../common/mixins";

.related-products {
  display: block;
  margin-bottom: 26px;
}

.related-products__title {
  font-weight: 500;
  color: $title-color;
  font-size: 36px;
  line-height: 1.4;
  margin-bottom: 17px;

  @media (max-width: 900px) {
    text-align: center;
    font-size: 32px;
    margin-bottom: 24px;
  }

  @media (max-width: 425px) {
    font-size: 24px;
    line-height: 1.1;
  }
}

.related-products__flex {
  display: flex;
  flex-wrap: wrap;
  margin-left: -7px;
  margin-right: -7px;

  @media (max-width: 900px) {
    justify-content: center;
  }

  @media (max-width: 425px) {
    margin-left: -3px;
    margin-right: -3px;
  }
}

.related-products__item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 13px 24px;
  background-color: white;
  box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
  border-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  color: $title-color;
  margin-left: 7px;
  margin-right: 7px;
  margin-bottom: 14px;
  text-decoration: unset;

  &:hover {
    box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.6);
  }

  @media (max-width: 425px) {
    padding: 7px 11px;
    font-size: 12px;
    line-height: 2.1;
    margin-left: 3px;
    margin-right: 3px;
    margin-bottom: 6px;
  }
}

.related-products__item-img {
  margin-right: 10px;
}
