@import "../common/variables";
@import "../common/mixins";

.catalogs {
  display: block;
  margin-bottom: 35px;
}

.catalogs__title {
  margin-bottom: 15px;
}

.catalogs__flex {
  display: flex;
  flex-wrap: wrap;
  margin-left: -7px;
  margin-right: -7px;

  @media (max-width: 900px) {
    justify-content: center;
  }
}

.catalogs__item {
  padding: 13px 24px 13px 64px;
  background-color: white;
  box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
  border-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  color: $title-color;
  margin-left: 7px;
  margin-right: 7px;
  margin-bottom: 14px;
  text-decoration: unset;
  position: relative;

  &:hover {
    box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.6);
  }


  &:after {
    content: "";
    position: absolute;
    left: 24px;
    top: 13px;
    width: 26px;
    height: 26px;
    background: url("../media/pdf-icon.svg");
  }
}
