@import "../common/variables";
@import "../common/mixins";

.additional {
  display: block;
  margin-bottom: 50px;
}

.additional__title {
  @media (min-width: 901px) {
    margin-bottom: 30px;
  }
}

.additional__3d-flex {
  display: flex;
  margin-top: 47px;

  @media (max-width: 900px) {
    flex-direction: column-reverse;
    margin-top: unset;
  }
}

.additional__3d-content {
  position: relative;
  z-index: 1;
  margin-top: 3px;

}

.additional__3d-content-title {
  font-weight: 500;
  font-size: 13px;
  line-height: 1.4;
  margin-bottom: 6px;
  color: $gray;
  text-transform: uppercase;
}

.additional__content {
  margin-bottom: 53px;
  margin-right: 13px;

  p {
    color: $text-color;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    margin-bottom: 1.2em;
  }

  a {
    color: inherit;
    text-decoration: unset;

    &:hover {
      color: $orange;
    }
  }
}

.additional__consultation {
  padding: unset;
  max-width: unset;
  width: unset;
  margin: unset;
}

.additional__form-wrap {
  padding: 29px 48px 17px;
  align-self: center;
  background: rgba(255, 255, 255, 0.42);
  margin-right: -75px;

  @media (max-width: 900px) {
    margin-left: auto;
    margin-right: auto;
  }
}

.additional__3d {
  border-radius: 15px;
  overflow: hidden;
  flex-shrink: 0;
  width: 713px;
  position: relative;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.16);

  &:after {
    content: "";
    display: block;
    width: 65px;
    height: 54px;
    position: absolute;
    top: 21px;
    right: 19px;
    background-image: url("../media/3d-icon.svg");
    background-size: contain;
    background-repeat: no-repeat;

    @media (max-width: 600px) {
      width: 32px;
      height: 27px;
      top: 12px;
      right: 14px;
    }
  }

  &--black:after {
    background-image: url("../media/3d-icon-black.svg");
  }

  @media (max-width: 1240px) {
    max-width: 500px;
    align-self: flex-start;
  }

  @media (max-width: 900px) {
    max-width: 713px;
    align-self: center;
    margin-bottom: 40px;
  }

  @media (max-width: 900px) {
    max-width: 100%;
  }
}


.additional__banner-wrap {
  border-radius: 20px;
  box-shadow: 0 4px 14px 1px rgba(0, 0, 0, 0.16);
  overflow: hidden;
  margin-left: -63px;

  @media (max-width: 1100px) {
    width: 650px;
  }

  @media (max-width: 1024px) {
    width: 530px;
    height: 360px;
  }

  @media (max-width: 900px) {
    order: -1;
    margin-left: unset;
    width: 100%;
    height: auto;
    margin-bottom: 23px;
  }
}

.additional__banner {
  @media (max-width: 900px) {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.additional__flex {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;

  .additional__content {
    margin-bottom: 33px;

    p {
      font-weight: 500;
    }
  }

  @media (max-width: 900px) {
    justify-content: center;
  }
}
