@import "../common/variables";
@import "../common/mixins";

.product-slider {
  max-width: 590px;

  @media (max-width: 1023px) {
    max-width: 390px;
  }

  @media (max-width: 900px) {
    max-width: 590px;
    width: 100%;
    align-self: center;
    margin-bottom: 40px;
  }
}

.product-slider__main {
  margin-bottom: 5px;
  border-radius: 10px;
  overflow: hidden;

  @media (max-width: 425px) {
    margin-bottom: 18px;
  }
}

.product-slider__gallery {
  .slick-list {
    margin-left: -5px;
    margin-right: -5px;
    width: calc(100% + 10px);
  }

  .slick-slide {
    margin-left: 5px;
    margin-right: 5px;
  }

  .slick-dots {
    li {
      width: unset;
      height: unset;
      margin-left: 3;
      margin-right: 2px;

      button {
        display: block;
        width: 11px;
        height: 11px;
        border-radius: 50%;
        background-color: #C4C4C4;

        &:before {
          content: unset;
        }
      }

      &.slick-active button {
        background-color: $title-color;
      }
    }
  }

  .slick-dots {
    bottom: -30px;
  }

  .slick-arrow {
    width: 34px;
    height: 34px;
    background-color: white;
    box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
    border-radius: 10px;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    &:before {
      content: "";
      display: block;
      width: 8px;
      height: 11px;
      background: url(../media/arrow-slick.svg) center no-repeat;
      background-size: contain;
    }

    &.slick-next:before {
      transform: rotate(180deg);
    }

    &.slick-prev {
      left: -16px;
    }

    &.slick-next {
      right: -16px;
    }
  }

  @media (max-width: 767px) {
    margin-left: -22px;
    margin-right: -22px;
    width: calc(100% + 44px);
  }
}

.product-slider__gallery-slide {
  border-radius: 10px;
  overflow: hidden;
}

.product-slider__gallery-img {
  width: 100%;
  height: auto;
}
