@import "../common/variables";
@import "../common/mixins";

.indicator {
  @include container;

  position: relative;
}

.indicator__box {
  position: absolute;
  top: 0;
  left: -30px;
  display: inline-flex;
  align-items: center;
  transform-origin: top left;
  transform: rotate(-90deg) translateX(-100%);
}

.indicator__counter {
  font-size: 16px;
  line-height: 1.6;
  font-weight: 500;
}

.indicator__current-value {
  font-size: 18px;
}

.indicator__amount {
  color: $gray;
}

.indicator__line-wrap {
  width: 160px;
  height: 4px;
  border-radius: 10px;
  background-color: #dfdfdf;
  margin-left: 28px;
}

.indicator__line {
  height: 100%;
  width: 33%;
  border-radius: 10px;
  background-color: $orange;
  margin-left: auto;
}
