@import "../common/variables";
@import "../common/mixins";

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
  border-radius: 10px;
  padding-top: 35px;
  padding-bottom: 28px;
  background-color: $bg-color;
  z-index: 9;

  @media (max-width: 768px) {
    position: relative;
    box-shadow: unset;
    border-radius: unset;
  }
}

.header__container {
  @include container;

  transition: 0.3s;
}

.header__flex {
  display: flex;
  justify-content: space-between;

  @media (max-width: 768px) {
    flex-wrap: wrap;
  }
}

.header__flex-wrap {
  flex-grow: 1;
  position: relative;
  margin-left: 40px;

  @media (max-width: 768px) {
    width: 100%;
    order: 1;
    margin-left: unset;
  }
}

.header__logo-wrap {
  @media (min-width: 1024px) {
    margin-left: -11px;
  }
}

.header__mob-menu {
  display: flex;
  align-items: flex-start;
  position: relative;
  justify-content: space-between;

  @media (max-width: 1023px) {
    width: 100%;
    height: calc(100vh - 110px);
    flex-direction: column;
    position: fixed;
    top: 110px;
    left: 110vw;
    z-index: 0;
    background: $bg-color;
    align-items: center;
    justify-content: flex-start;
    padding: 58px 0 58px;
    opacity: 0;

    &.active {
      left: 0;
      opacity: 1;
      animation: showMenu 0.1s ease-in-out;

      @keyframes showMenu {
        0% {
          left: 0;
          opacity: 0;
        }

        100% {
          opacity: 1;
        }
      }
    }
  }

  @media (max-width: 768px) {
    z-index: 13;
    top: 85px;
    height: calc(100vh - 85px);
    overflow: auto;
  }
}

.header__menu {
  flex-grow: 1;
  max-width: 625px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media (max-width: 1100px) {
    max-width: 555px;
  }

  @media (max-width: 1023px) {
    flex-grow: unset;
    flex-direction: column;
    width: 100%;
  }
}

.header__form {
  position: absolute;
  width: 100%;
  max-width: 625px;
  left: 0;
  bottom: 0;
  z-index: 11;

  @at-root .header__mob-menu.active + #{&} {
    left: -110vw;
  }

  @media (max-width: 1100px) {
    max-width: 555px;
  }

  @media (max-width: 768px) {
    position: relative;
    margin-top: 13px;
    max-width: unset;
  }
}

.header__search {
  position: relative;
  width: 100%;
  height: 50px;
  border-radius: 10px;
  border: unset;
  padding: 10px 48px;
  box-shadow: inset 3px 3px 10px 1px rgba(51, 44, 56, 0.2);
  outline: unset;
  font-size: 24px;
  color: #616161;

  &::placeholder {
    color: $gray;
    text-align: center;
  }

  &:not(:invalid) {
    z-index: 12;
    box-shadow: unset;
    border-bottom-left-radius: unset;
    border-bottom-right-radius: unset;
  }

  &:invalid + .header__search-placeholder {
    display: flex;
  }

  &:focus + .header__search-placeholder {
    display: none;
  }

  &:invalid ~ .header__search-modal {
    display: none;
  }

  &:invalid ~ .header__search-back {
    display: none;
  }

  &:invalid ~ .header__search-close {
    display: none;
  }
}

.header__search-placeholder {
  display: none;
  color: $gray;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  span {
    white-space: nowrap;
  }
}

.header__search-results {
  position: relative;
  z-index: 1;
}

.header__search-back {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 13;

  svg {
    path {
      transition: 0.3s;
    }
  }

  &:hover {
    path {
      fill: $orange;
    }
  }
}

.header__search-close {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 13;

  svg {
    path {
      transition: 0.3s;
    }
  }

  &:hover {
    path {
      fill: $orange;
    }
  }
}

.header__social-list {
  @include header-blocks-hidden;

  margin-top: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__social-link {
  display: inline-block;
  width: 30px;
  height: 30px;
  text-decoration: unset;
  margin-left: 6px;
  margin-right: 6px;
  border-radius: 50%;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  transition: 0.3s;

  &:hover {
    box-shadow: 0 4px 16px rgba(51, 44, 56, 0.3);
  }
}

.header__btn {
  color: #3A3A3A;
  transition: 0.2s;
  margin-top: 5px;

  @at-root .header.scroll #{&} {
    margin-top: 8px;
  }
}

.header__menu-btn {
  display: none;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  padding: 9px;
  position: relative;
  z-index: 1;
  cursor: pointer;
  border: unset;
  margin-left: 30px;
  margin-top: -20px;
  align-self: center;
  padding: unset;

  @media (max-width: 1023px) {
    display: flex;
    justify-content: flex-end;
  }
}

.header__menu-btn-line {
  width: 21px;
  height: 3px;
  background: $title-color;
  position: relative;
  transition: background 0s .2s;
  border-radius: 5px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    height: 3px;
    background: $title-color;
    right: 0;
    transition-duration: .2s, .2s;
    transition-delay: .2s, 0s;
    border-radius: 5px;
  }

  &::before {
    width: 21px;
    top: -6px;
    transition-property: top, transform;
  }

  &::after {
    width: 21px;
    bottom: -6px;
    transition-property: bottom, transform;
  }

  @at-root .header__menu-btn.active #{&} {
    background: transparent;

    &::before,
    &::after {
      width: 100%;
      transition-delay: 0.4s, .2s;
    }

    &::before {
      top: 0;
      transform: rotate(45deg);
      transition-property: transform, top;
    }

    &::after {
      bottom: 0;
      transform: rotate(-45deg);
      transition-property: transform, bottom;
    }
  }
}

.header__search-modal {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 11;
  overflow-y: auto;
  height: calc(100vh - 150px);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.header__search-dark-bg {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(30, 30, 30, 0.9);
}

.header__result-wrap {
  display: flex;
  flex-direction: column;
  background: #fff;

  &:last-of-type {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
}

.header__result-title {
  background: #EEEEEE;
  box-shadow: 0 3px 0 rgba(58, 62, 62, 0.15);
  font-size: 14px;
  line-height: 1.5;
  padding: 7px 48px;
  color: #616161;
}

.header__result-link {
  position: relative;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.3;
  padding: 12px 48px;
  color: #616161;
  text-decoration: unset;
  border-bottom: 3px solid rgba(58, 62, 62, 0.15);
  transition: 0.3s;

  &:after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 16px;
    display: block;
    width: 24px;
    height: 24px;
    background: url(../media/search-arrow.svg);
  }

  &:hover {
    color: $orange;

    &:after {
      background: url(../media/search-arrow-hover.svg);
    }
  }

  @at-root .header__result-wrap:not(.show) #{&}:nth-of-type(n + 4) {
    opacity: 0;
    height: 0;
    padding-top: 0;
    padding-bottom: 0;
    color: transparent;
  }
}

.header__result-btn {
  font-size: 18px;
  line-height: 1.5;
  color: $orange;
  padding: 4px 48px;
  border: unset;
  background: unset;
  text-align: left;
  position: relative;
  cursor: pointer;

  &:before {
    content: "";
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 18px;
    top: 50%;
    transform: translateY(-50%);
    height: 1px;
    font-size: 9px;
  }

  span {
    display: none;

    &.header__result-btn-text-show {
      display: inline;
    }
  }

  @at-root .header__result-wrap.show .header__result-btn-text-hide {
    display: inline;
  }

  @at-root .header__result-wrap.show .header__result-btn-text-show {
    display: none;
  }
}
