@import "../common/variables";
@import "../common/mixins";

.nav {
  @include header-blocks-hidden;

  margin-top: 14px;
  margin-bottom: 25px;

  @media (max-width: 1023px) {
    width: 100%;
  }
}

.nav__list {
  display: flex;

  @media (max-width: 1023px) {
    flex-direction: column;
  }
}

.nav__link {
  text-decoration: unset;
  color: $title-color;
  transition: 0.3s;
  position: relative;
  font-size: 18px;
  line-height: 1.55;
  margin-left: 17px;
  margin-right: 17px;

  &:not(.active):hover {
    color: $orange;
  }

  &.active {
    pointer-events: none;

    &:before {
      content: "";
      position: absolute;
      display: block;
      width: 5px;
      height: 5px;
      background-color: $orange;
      border-radius: 50% ;
      top: 40%;
      transform: translateY(-50%);
      left: -10px;
    }
  }

  @media (max-width: 1023px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 13px 17px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
    text-decoration: unset;
    color: $title-color;
    font-size: 16px;
    line-height: 1.55;
    font-weight: 500;
    transition: 0.3s;

    &:not(:last-of-type) {
      margin-bottom: 7px;
    }

    &.active {
      color: white;
      background-color: $orange;

      &:before {
        content: none;
      }
    }
  }
}

.nav__link-icon {
  --color-1: #3A3E3E;
  width: 8px;
  height: 12px;
  transition: 0.3s;
  margin-left: 20px;

  @at-root .nav__link.active #{&} {
    --color-1: white;
  }

  @media (min-width: 1024px) {
    display: none;
  }
}
