@import "../common/variables";
@import "../common/mixins";

.breadcrumbs {
  display: flex;
  margin-bottom: 10px;
}

.breadcrumbs__item {
    &:not(:first-of-type) {
    margin-left: 20px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: -10px;
      top: 5px;
      display: block;
      width: 3px;
      height: 3px;
      border-radius: 50%;
      background-color: $gray;
    }
  }

  &:last-of-type .breadcrumbs__link {
    pointer-events: none;
  }

  &:not(:last-of-type):hover:before {
    background-color: $orange;
  }
}

.breadcrumbs__link {
  color: $gray;
  font-size: 13px;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: unset;
  transition: 0.3s;

  &:hover {
    color: $orange;
  }
}

@media (max-width: 900px) {
  .breadcrumbs {
    display: none;
  }
}
