@import "../common/variables";
@import "../common/mixins";

.article {
  display: block;
}

.article__content {
  margin-bottom: 56px;

  p {
    color: $text-color;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.05;
    margin-bottom: 1em;
  }

  .article__media-wrap {
    margin: 30px auto;
    display: flex;
    align-items: center;
    justify-content: center;

    iframe {
      border-radius: 10px;
    }

    img {
      border-radius: 10px;
    }
  }

  .article__media-wrap-float {
    float: right;
    margin-left: 34px;

    iframe {
      border-radius: 10px;
    }

    img {
      border-radius: 10px;
    }

    @media (max-width: 1024px) {
      margin-bottom: 30px;
      float: unset;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: unset;
    }
  }
}
