@import "../common/variables";
@import "../common/mixins";

.page {
  padding-top: 213px;

  &--no-scroll {
    overflow: hidden;
  }

  @media (max-width: 1023px) {
    padding-top: 164px;
  }

  @media (max-width: 768px) {
    padding-top: unset;
  }
}

.page__container {
  @include container;
}

.page__title {
  font-size: 50px;
  line-height: 1;
  font-weight: 500;
  color: $title-color;
  margin-bottom: 10px;
}

@media (min-width: 768px) and (max-width: 900px)  {
  .page__title {
    line-height: 1.42;
  }
}

@media (max-width: 900px) {
  .page__title {
    text-align: center;
    font-size: 32px;
    margin-bottom: 28px;
  }
}

@media (max-width: 425px) {
  .page__title {
    font-size: 24px;
    margin-bottom: 24px;
  }
}
