@import "../common/variables";
@import "../common/mixins";

.projects {
  display: block;
  margin-bottom: 12px;
}

.projects__title {
  margin-bottom: 30px;
}

.projects__subtitle {
  color: $title-color;
  font-weight: 500;
  font-size: 36px;
  line-height: 1.44;
  margin-top: 32px;
  margin-bottom: 30px;

  @media (max-width: 900px) {
    text-align: center;
    font-size: 32px;
    margin-bottom: 28px;
    line-height: 1;
  }

  @media (max-width: 425px) {
    font-size: 24px;
    margin-bottom: 24px;
  }
}

.projects__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 30px;
}

.projects__item {
  position: relative;
  width: calc(33.333% - 9px);
  margin-bottom: 18px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 4px 14px 1px rgba(0, 0, 0, 0.16);
  transition: all .5s;

  &:hover {
    box-shadow: 6px 6px 9px #545159;
  }
}

.projects__img-wrap {
  position: relative;
  transform: translateZ(0);
  border-radius: 10px;
  overflow: hidden;

  &:before {
    content: "";
    position: absolute;
    display: block;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(180deg, transparent, rgba(0, 0, 0, 0.5) 100%);
    z-index: 1;
    border-radius: 10px;
  }
}

.projects__img {
  transition: all 0.5s;
  will-change: transform;
  border-radius: 10px;
  overflow: hidden;

  @at-root .projects__item:hover #{&} {
    transform: scale(1.1);
  }
}

.projects__item-title {
  position: absolute;
  left: 12px;
  right: 12px;
  bottom: 15px;
  text-decoration: unset;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.2;
  color: white;
  z-index: 1;
}

.projects__link-to-all {
  width: calc(33.333% - 12px);
  margin-bottom: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.projects__link-box {
  background: #f4f5f7;
  box-shadow: 0 4px 14px 1px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  max-width: 280px;
  padding: 22px 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects__link-text {
  text-align: center;
  font-size: 16px;
  line-height: 1.2;
  margin-bottom: 20px;
  color: $text-color;
}

.projects__link-btn {
  min-width: 189px;
}

@media (max-width: 900px) {
  .projects {
    margin-bottom: 47px;
  }

  .projects__item {
    width: calc(50% - 12px);
    margin-bottom: 24px;
  }

  .projects__link-to-all {
    width: 100%;
  }

  .projects__link-box {
    max-width: 466px;
    width: 100%;
  }
}

.projects__menu {
  display: flex;
  flex-wrap: wrap;
  margin-left: -7px;
  margin-right: -7px;
  margin-bottom: 16px;

  @media (max-width: 768px) {
    justify-content: center;
    margin-bottom: 10px;
  }

  @media (max-width: 425px) {
    margin-left: -3px;
    margin-right: -3px;
  }
}

@media (max-width: 768px) {
  .projects {
    margin-bottom: 40px;
  }
}

.projects__menu-item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 13px 24px;
  background-color: white;
  box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
  border-radius: 10px;
  font-size: 16px;
  line-height: 1.55;
  color: $title-color;
  font-weight: 500;
  margin-left: 7px;
  margin-right: 7px;
  margin-bottom: 14px;
  text-decoration: unset;

  &:hover {
    box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.6);
  }

  &.active {
    color: white;
    background-color: $orange;
    &:hover {
      box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
    }
  }

  @media (max-width: 425px) {
    padding: 7px 20px;
    font-size: 12px;
    line-height: 2.1;
    margin-left: 3px;
    margin-right: 3px;
    margin-bottom: 8px;
  }
}

@media (max-width: 600px) {
  .projects__item {
    width: 100%;
    margin-bottom: 10px;
  }

  .projects__link-to-all {
    margin-top: 10px;
  }
}

@media (max-width: 425px) {
  .projects__title {
    margin-bottom: 24px;
  }
}
