@import "../common/variables";
@import "../common/mixins";

.contacts {
  display: block;
  margin-bottom: 30px;
  overflow: hidden;

  @media (max-width: 900px) {
    margin-bottom: 40px;
  }
}

.contacts__flex {
  display: flex;

  @media (max-width: 900px) {
    justify-content: center;
  }
}

.contacts__info {
  margin-right: -129px;
  z-index: 1;
  max-width: 565px;
  width: 100%;

  @media (max-width: 1023px) {
    max-width: 55vw;
    flex-shrink: 0;
  }

  @media (max-width: 900px) {
    max-width: 565px;
    align-self: center;
    margin-right: unset;
  }
}

.contacts__title {
  margin-bottom: 23px;
}

.contacts__menu {
  margin-bottom: 6px;
  padding-left: 10px;
  display: flex;

  @media (max-width: 900px) {
    justify-content: center;
    margin-bottom: 15px;
  }
}

.contacts__btn {
  display: inline-block;
  background-color: white;
  border: unset;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.6;
  padding: 10px 19px;
  border-radius: 10px 10px 0 0;
  cursor: pointer;
  color: $gray;

  &.active {
    position: relative;
    color: $title-color;
    order: -1;
    margin-right: 18px;

    &:before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: -10px;
      bottom: -6px;
      left: -10px;
      border-radius: 10px 10px 0 0;
      background-color: white;
      z-index: -1;

      @media (max-width: 900px) {
        display: none;
      }
    }

    @media (max-width: 900px) {
      background-color: $orange;
      color: white;
      order: unset;
      margin-right: unset;
    }
  }

  @media (max-width: 900px) {
    border-radius: 10px;
    min-width: 126px;
    font-size: 12px;

    &:first-of-type {
      margin-right: 5px;
    }
  }
}

.contacts__tabs {
  display: block;
}

.contacts__tab {
  display: none;
  background-color: white;
  border-radius: 0 20px 20px 20px;
  padding: 32px 24px;

  &.active {
    display: block;
  }

  @media (max-width: 900px) {
    border-radius: 20px;
  }
}

.contacts__tab-title {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
  margin-bottom: 22px;
  text-align: center;

  @media (min-width: 900px) {
    text-align: left;
  }
}

.contacts__tab-content {
  color: $title-color;
  font-size: 16px;
  line-height: 1.5;

  a {
    text-decoration: unset;
    transition: 0.3s;
    color: inherit;

    &:hover {
      color: $orange;
    }
  }

  &--phones {
    display: flex;
    flex-wrap: wrap;
    margin-top: 5px;
  }
}

.contacts__tab-content-phone {
  min-width: 167px;
  margin-bottom: 7px;
}

.contacts__tab-row {
  display: flex;
  align-items: center;
  margin-bottom: 18px;

  &:last-of-type {
    margin-bottom: unset;
  }
}

.contacts__tab-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  box-shadow: 0 4px 16px rgba(51, 44, 56, 0.3);
  backdrop-filter: blur(4px);
  margin-right: 14px;
  flex-shrink: 0;
  align-self: flex-start;
}

.contacts__maps {
  margin-right: -164px;

  @media (max-width: 900px) {
    display: none;
  }
}

.contacts__map {
  display: none;
  border-radius: 20px;
  overflow: hidden;

  &.active {
    display: block;
  }
}
