@import "../common/variables";
@import "../common/mixins";

.product-card {
  display: block;
  margin-bottom: 30px;

  @media (max-width: 900px) {
    margin-bottom: 40px;
  }

  @media (max-width: 425px) {
    margin-bottom: 25px;
  }
}

.product-card__container {
  @include container;
}

.product-card__title {
  margin-bottom: 35px;
}

.product-card__flex {
  display: flex;

  @media (max-width: 900px) {
    flex-direction: column-reverse;
  }
}

.product-card__info {
  width: 100%;
  max-width: 466px;
  margin-right: 34px;

  @media (max-width: 900px) {
    max-width: 590px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
  }
}

.product-card__info-title {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.2;
  margin-bottom: 13px;
  color: $gray;
}

.product-card__content {
  p {
    color: $text-color;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.2;
    margin-bottom: 1em;
  }
}

.product__card-btn {
  display: inline-block;

  @media (max-width: 900px) {
    align-self: center;
  }

  @media (max-width: 425px) {
    display: none;
  }
}
