@import "../common/variables";
@import "../common/mixins";

.quick-consultation {
  display: block;
  margin-bottom: 30px;

  &--modal {
    padding: unset;

    .quick-consultation__form-wrap {
      border-radius: unset;
    }
  }
}

.quick-consultation__container {
  @include container
}


.quick-consultation__flex {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.quick-consultation__form-wrap {
  padding: 44px 48px 32px;
  background: rgba(255, 255, 255, 0.72);
  box-shadow: inset 0 4px 14px rgba(51, 44, 56, 0.16);
  backdrop-filter: blur(31px);
  border-radius: 10px;
  max-width: 457px;

  .form__input::placeholder {
    text-align: center;
  }

  .form__btn {
    margin-left: auto;
    margin-right: auto;
  }
}

.quick-consultation__form-subtitle {
  font-weight: 600;
  font-size: 18px;
  line-height: 1.2;
  color: $text-color;
  text-align: center;
  margin-bottom: 8px;
}

.quick-consultation__form-text {
  text-align: center;
  font-size: 18px;
  line-height: 1.2;
  margin-bottom: 23px;

  &--small {
    font-size: 14px;
    max-width: 250px;
    margin-left: auto;
    margin-right: auto;
  }
}


@media (max-width: 900px) {
  .quick-consultation__flex {
    justify-content: center;
  }

  .quick-consultation__img-wrap {
    display: none;
  }
}

@media (max-width: 500px) {
  .quick-consultation__form-wrap {
    padding: 39px 19px 25px;
    box-shadow: 3px 3px 5px 1px rgba(51, 44, 56, 0.16);
  }
}
