@import "../common/variables";
@import "../common/mixins";

.examples {
  display: block;
  margin-bottom: 80px;
}

.examples__container {
  @include container;
}

.examples__title {
  font-weight: 500;
  font-size: 36px;
  line-height: 1.39;
  color: $title-color;
  margin-bottom: 30px;

  @media (max-width: 900px) {
    text-align: center;
    font-size: 32px;
    margin-bottom: 28px;
    line-height: 1;
  }

  @media (max-width: 425px) {
    font-size: 24px;
    margin-bottom: 24px;
  }
}

.examples__slider {
  .slick-list {
    margin-left: -5px;
    margin-right: -5px;
    width: calc(100% + 10px);
  }

  .slick-slide {
    margin-left: 5px;
    margin-right: 5px;
  }

  .slick-dots {
    li {
      width: unset;
      height: unset;
      margin-left: 3;
      margin-right: 2px;

      button {
        display: block;
        width: 11px;
        height: 11px;
        border-radius: 50%;
        background-color: #C4C4C4;

        &:before {
          content: unset;
        }
      }

      &.slick-active button {
        background-color: $title-color;
      }
    }
  }

  .slick-dots {
    bottom: -30px;
  }

  .slick-arrow {
    width: 34px;
    height: 34px;
    background-color: white;
    box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
    border-radius: 10px;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    &:before {
      content: "";
      display: block;
      width: 8px;
      height: 11px;
      background: url(../media/arrow-slick.svg) center no-repeat;
      background-size: contain;
    }

    &.slick-next:before {
      transform: rotate(180deg);
    }

    &.slick-prev {
      left: -16px;
    }

    &.slick-next {
      right: -16px;
    }
  }

  @media (max-width: 768px) {
    margin-left: -22px;
    margin-right: -22px;
    width: calc(100% + 44px);
  }
}

.examples__img-wrap {
  display: block;
  border-radius: 10px;
  overflow: hidden;
}
