@import "../common/variables";
@import "../common/mixins";

.btn {
  display: block;
  outline: none;
  border: unset;
  background-color: $orange;
  border-radius: 10px;
  color: white;
  line-height: 1.5;
  text-align: center;
  padding: 11px 22px;
  font-weight: 500;
  cursor: pointer;
  transition: 0.3s;
  box-shadow: 0 4px 14px rgba(58, 62, 62, 0.3);
  text-decoration: unset;

  &:hover {
    background-color: $dark-orange;
    box-shadow: 0 4px 20px rgba(58, 62, 62, 0.3);
  }
}
