@import "../common/variables";
@import "../common/mixins";

$pageClassesColorGreen: ".template-career-page-it-guru";

.form {

  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px white inset !important;
  }

  input[type="text"],
  input[type="email"],
  input[type="number"],
  input[type="password"],
  input[type="tel"],
  select,
  textarea {
    font-family: $main-font;
    color: $text-color;
    border-left: unset;
    border-right: unset;
    border-top: unset;
    border-bottom: unset;
    font-size: 16px;
    width: 100%;
    display: block;
    transition: 0.25s;
    border-radius: 10px;
    outline: none;
    background-color: inherit;
    resize: none;
    line-height: 1.5;
    padding: 17px 31px;
    box-shadow: inset 3px 3px 10px 1px rgba(51, 44, 56, 0.2);
    margin-bottom: 17px;

    &::placeholder {
      color: #a2a2a2;
    }
  }

  input[type="text"],
  input[type="email"],
  input[type="number"],
  input[type="password"],
  input[type="tel"],
  select {
    &:focus {
      outline: none;
    }
  }

  input[type="file"] {
    display: none;
  }

  label {
    z-index: 1;

    &.active {
      transform: translateY(calc(-50% - 25px));
      font-size: 12px;
    }

    &.form__label--file {
      cursor: pointer;
      position: relative;
      padding-left: 50px;
      display: flex;
      align-items: center;

      &::after {
        content: "";
        position: absolute;
        display: block;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        -webkit-mask-size: auto;
        mask-size: auto;
        mask-repeat: no-repeat;
        -webkit-mask-repeat: no-repeat;

      }
    }
  }

  textarea {
    height: 200px;
    border-radius: 5px;
    padding: 17px 31px;

    &:focus {
      outline: none;
    }
  }

  input[type="checkbox"],
  input[type="radio"] {
    display: none;
  }

  input[type="checkbox"] + label {
    position: relative;
    font-size: 14px;
    padding-left: 35px;
    margin-bottom: 10px;
    margin-top: 12px;
    cursor: pointer;
    color: black;
    text-align: left;

    a {
      color: black;
      text-decoration: underline;
    }

    &::before {
      content: "";
      position: absolute;
      display: inline-block;
      left: 0;
      top: 5px;
      transform: none;
      vertical-align: middle;
      width: 25px;
      height: 25px;
      border: 1px solid red;
      transition: 0.3s;
    }

    &::after {
      content: "";
      position: absolute;
      display: inline-block;
      left: 0;
      top: 5px;
      vertical-align: middle;
      transform: scale(0);
      width: 25px;
      height: 25px;
      transition: 0.2s;
      background: url(../images/arrow-bottom.svg) center / 20px 20px no-repeat;
    }
  }

  input[type="radio"] + label {
    position: relative;
    font-size: 14px;
    padding-left: 32px;
    margin-bottom: 10px;
    margin-top: 12px;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      display: inline-block;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      vertical-align: middle;
      width: 22px;
      height: 22px;
      border-radius: 50%;
      border: 1px solid red;
    }

    &::after {
      content: "";
      position: absolute;
      display: inline-block;
      left: 6px;
      top: 50%;
      vertical-align: middle;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: red;
      transform: translateY(-50%) scale(0);
      transition: 0.3s;
    }
  }

  input[type="radio"]:checked + label {
    &::after {
      content: "";
      transform: translateY(-50%);
    }
  }

  input[type="checkbox"]:checked + label {
    &::after {
      transform: scale(1);
    }
  }
}

.form__btn {
  min-height: 50px;
}

.form__file-wrap {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.form__file-btn {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: $title-color;
  padding: 7px 13px;
  text-align: center;
  margin-right: 20px;
  background-color: white;
  box-shadow: 0 4px 14px 1px rgba(51, 44, 56, 0.3);
  border-radius: 10px;
}

.form__file-text-input {
  font-size: 12px;
  line-height: 1.5;
  color: $gray;
}

.form__modal-success {
  padding: 48px 56px;
  background: rgba(255, 255, 255, 0.72);
  box-shadow: inset 0 4px 14px rgba(51, 44, 56, 0.16);
  backdrop-filter: blur(31px);
  border-radius: 10px;

  p {
    font-size: 18px;
    line-height: 1.2;
    color: $text-color;
  }
}
