@import "../common/variables";
@import "../common/mixins";

.home-menu {
  display: block;
  overflow: hidden;
  padding-bottom: 30px;
}

.home-menu__title {
  max-width: 590px;
}

.home-menu__wrap {
  display: flex;
  align-items: center;
  margin-top: -28px;
}

.home-menu__list {
  display: flex;
  flex-direction: column;
  max-width: 445px;
  width: 100%;
  flex-shrink: 0;
  background: rgba(255, 255, 255, 0.72);
  box-shadow: inset 0 4px 14px rgba(51, 44, 56, 0.16);
  backdrop-filter: blur(31px);
  border-radius: 10px;
  padding: 45px 31px 35px;
  margin-right: -100px;
  z-index: 1;
}

.home-menu__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 13px 17px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 16px 1px rgba(51, 44, 56, 0.3);
  text-decoration: unset;
  color: $title-color;
  font-size: 16px;
  line-height: 1.55;
  font-weight: 500;
  transition: 0.3s;

  &:not(:last-of-type) {
    margin-bottom: 7px;
  }

  &.active {
    @media (min-width: 901px) {
      color: white;
      background-color: $orange;
      margin-right: -74px;
    }
  }
}

.home-menu__img-wrap {
  height: 620px;
  width: 920px;
  margin-right: -175px;
  border-radius: 10px;
  overflow: hidden;
  filter: drop-shadow(0px 4px 14px rgba(58, 62, 62, 0.3));
}

.home-menu__img {
  display: none;
  width: 100%;
  height: 100%;
  object-fit: cover;

  &.active {
    display: block;
    animation: fadeEffect 0.55s ease-out;

    @keyframes fadeEffect {
      0% {
        opacity: 0.7;
        display: block;
      }

      100% {
        opacity: 1;
      }
    }
  }
}

.home-menu__item-icon {
  --color-1: #3A3E3E;
  width: 8px;
  height: 12px;
  transition: 0.3s;
  margin-left: 20px;

  @media (min-width: 901px) {
    @at-root .home-menu__item.active #{&} {
      --color-1: white;
    }
  }
}

@media(max-width: 900px) {
  .home-menu {
    padding-bottom: 40px;
  }

  .home-menu__title {
    max-width: unset;
  }

  .home-menu__wrap {
    margin-top: unset;
    justify-content: center;
  }

  .home-menu__list {
    margin-right: unset;
    background: unset;
    padding: unset;
    box-shadow: unset;
    backdrop-filter: unset;
    max-width: 570px;
  }

  .home-menu__img-wrap {
    display: none;
  }

  .home-menu__item {
    box-shadow: unset;
  }

}
