.form label.form__label--file::after {
  content: "";
  position: absolute;
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  -webkit-mask-size: auto;
  mask-size: auto;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
