.form input[type="checkbox"],
.form input[type="radio"] {
  display: none;
}

.form input[type="checkbox"] + label {
  position: relative;
  font-size: 14px;
  padding-left: 35px;
  margin-bottom: 10px;
  margin-top: 12px;
  cursor: pointer;
  color: black;
  text-align: left;
}

.form input[type="checkbox"] + label a {
  color: black;
  text-decoration: underline;
}

.form input[type="checkbox"] + label::before {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  top: 5px;
  transform: none;
  vertical-align: middle;
  width: 25px;
  height: 25px;
  border: 1px solid red;
  transition: 0.3s;
}

.form input[type="checkbox"] + label::after {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  top: 5px;
  vertical-align: middle;
  transform: scale(0);
  width: 25px;
  height: 25px;
  transition: 0.2s;
  background: url(../images/arrow-bottom.svg) center/20px 20px no-repeat;
}

.form input[type="radio"] + label {
  position: relative;
  font-size: 14px;
  padding-left: 32px;
  margin-bottom: 10px;
  margin-top: 12px;
  cursor: pointer;
}

.form input[type="radio"] + label::before {
  content: "";
  position: absolute;
  display: inline-block;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  vertical-align: middle;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: 1px solid red;
}

.form input[type="radio"] + label::after {
  content: "";
  position: absolute;
  display: inline-block;
  left: 6px;
  top: 50%;
  vertical-align: middle;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: red;
  transform: translateY(-50%) scale(0);
  transition: 0.3s;
}

.form input[type="radio"]:checked + label::after {
  content: "";
  transform: translateY(-50%);
}

.form input[type="checkbox"]:checked + label::after {
  transform: scale(1);
}

